import React from "react";
import { NavLink } from "react-router-dom";
import classes from "./nav.module.css";

function Nav() {
  return (
    <nav className={classes.nav}>
      <ul className="nav__list">
        <li className={classes.nav__item}>
          <NavLink
            className={classes.nav__link}
            activeClassName={classes.active__link}
            to="/Profile"
          >
            Profile
          </NavLink>
        </li>
        <li className={classes.nav__item}>
          <NavLink
            className={classes.nav__link}
            activeClassName={classes.active__link}
            to="/Dialogs"
          >
            Messages
          </NavLink>
        </li>
        <li className={classes.nav__item}>
          <NavLink
            className={classes.nav__link}
            activeClassName={classes.active__link}
            to="/News"
          >
            News
          </NavLink>
        </li>
        <li className={classes.nav__item}>
          <NavLink
            className={classes.nav__link}
            activeClassName={classes.active__link}
            to="/Music"
          >
            Music
          </NavLink>
        </li>
        <li className={classes.nav__item}>
          <NavLink
            className={classes.nav__link}
            activeClassName={classes.active__link}
            to="/Settings"
          >
            Settings
          </NavLink>
        </li>
      </ul>
    </nav>
  );
}

export default Nav;
