import React from "react";
import { NavLink } from "react-router-dom";
import classes from "./Dialogs.module.css";

const Dialog = (props) => {
  return (
    <div className={classes.dialogs__user}>
      <NavLink
        className={classes.dialogs__link}
        activeClassName={classes.link__active}
        to={"/Dialogs/" + props.name}
      >
        {props.name}
      </NavLink>
    </div>
  );
};

const Message = (props) => {
  return <div>{props.message}</div>;
};

const Dialogs = () => {
  return (
    <div className={classes.dialogs}>
      <div className={classes.dialogs__users}>
        <Dialog name="Dimych" id="1" />
        <Dialog name="Andrey" id="2" />
        <Dialog name="Sveta" id="3" />
        <Dialog name="Viktor" id="4" />
        <Dialog name="Masha" id="5" />
        <Dialog name="Sasha" id="6" />
      </div>
      <div className={classes.dialogs__messages}>
        <Message message="Hi" />
        <Message message="Yo" />
        <Message message="Privet" />
      </div>
    </div>
  );
};

export default Dialogs;
